import React from 'react'

export class Profile extends React.Component{
    render(){
        var containerStyle={
            width:'50%',
            
            display: 'contents',
            flexDirection: 'column',
            alignItems: 'center',
            justifyContent: 'center',
            
        };
        var imageStyle={
            margin:'5px 5px 10px 10px',
            width: '300px',            
            overflow: "hidden",    
            
        };
        var articleStyle={
        "border-right":"1px solid #000",
        "border-left":"1px solid #000",
        "word-wrap": "break-word",
        width: "400px",
        margin:'1px 10px 5px 10px',
        padding: '0px 5px 0px 5px'
        };
        return (
            <span className="Profile-container" style={containerStyle}>
                <img src={this.props.url} style={imageStyle} alt='profile-img' border='0'></img>
                <article style={articleStyle}>Hey! I'm {this.props.name} <br></br>{this.props.text}
                </article>
                
            </span>
            
        );
    }
}

export default Profile